 
 import { Button, Switch, Table } from "antd";
import { CheckboxGroupProps } from "antd/es/checkbox";
import { useState } from "react";
import "./App.css";
import AddStudent from "@/components/AddStudent";
import useGlobalStore from "@/store/my-store";

type StudentType = {
  id: number;
  firstName: string;
  lastName: string;
  age: number;
  gender: "male" | "female";
  active: boolean;
};

type GlobalStoreType = {
  students: StudentType[];
  groups: any[];
};

function SrudentsPage() {
  const state = useGlobalStore();

  const [open, setOpen] = useState(false);

  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  // const num: string[] = ["olma", "anor", "nok"];
  // const sum = num.reduce((prew, item, {}): any => {
  //   if (item.length > 0) {
  //     console.log(item);

  //     return prew.length;
  //   }

  //   return item.length;
  // });
  return (
    <>
      {/* {sum} */}
      <div className="flex gap-2 items-center ">
        <Button>Umumiy son: {state.students.length}</Button>
        <Button>
          Faollar:
          {
            state.students.filter((item: any) => {
              return item.active;
            }).length
          }
        </Button>
        <Button>
          Nofaollar:{" "}
          {state.students.reduce((count: any, item: any) => {
            return !item.active ? count + 1 : count;
          }, 0)}
        </Button>
      </div>
      <div className="flex flex-col my-5">
        <AddStudent onClose={onClose} open={open} showDrawer={showDrawer} />
        <Table
          columns={[
            {
              title: "ID",
              dataIndex: "id",
            },
            {
              title: "ID",
              dataIndex: "id",
            },
            {
              title: "Ism",
              dataIndex: "firstName",
            },
            {
              title: "Familiya",
              dataIndex: "lastName",
            },
            {
              title: "Yosh",
              dataIndex: "age",
            },
            {
              title: "Jinsi",
              dataIndex: "gender",
              render: (v, item: any) => {
                return (
                  <span key={item.id}>{v === "male" ? "Erkak" : "Ayol"}</span>
                );
              },
            },
            {
              title: "Active",
              dataIndex: "active",
              render: (v, studentt) => {
                return (
                  <Switch
                    checked={v}
                    onChange={(checked) => {
                      const new_students = state.students.map((item: any) => {
                        if (item.id === studentt.id) {
                          return {
                            ...item,
                            active: checked,
                          };
                        }
                        return item;
                      });
                      useGlobalStore.setState({ students: new_students });
                    }}
                  />
                );
              },
            },
          ]}
          dataSource={state.students}
        />
        <Button
          color="danger"
          type="text"
          variant="text"
          onClick={() => {
            localStorage.clear();
          }}
        >
          Hammasini ochirish{" "}
        </Button>
      </div>
    </>
  );
}

export default SrudentsPage;
